<template>
  <div class="univariate-monitor-chart-container">
    <div id="univariate-monitor-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import {getSingleVarMonitorData} from '@/api/monitor.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';

const keyToName = {
  count: '样本（总计）',
  passingCount: '样本（通过总数）',
  resultrate: '样本（通过率）',
  t10Late: '样本（t10指标逾期率）',
  avg: '平均值（总计）',
  pass: '平均值（通过）',
  refause: '平均值（建议拒绝）',
  isT10: '平均值（T10指标逾期）',
  notT10: '平均值（非T10指标逾期）'
};
const allColorObj = getColorObj();
const colorArr = [
  '总计',
  '通过总数',
  '通过率',
  'T10指标逾期率',
  '平均值总计',
  '平均值通过',
  '平均值拒绝',
  '平均值T10指标逾期',
  '平均值非T10指标逾期'
].map(key => allColorObj[key]);
export default {
  name: 'univariate-monitor-chart',
  data() {
    return {
      showChart: false
    };
  },
  created() {
    this.$on('drawUnivariateMonitorChart', (moduleName, varKey, sTime, eTime) => {
      this.drawFunc(moduleName, varKey, sTime, eTime);
    });
  },
  methods: {
    drawFunc(moduleName, varKey, sTime, eTime) {
      this.showChart = true;
      this.getData(moduleName, varKey, sTime, eTime)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#univariate-monitor-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(result.result);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(moduleName, varKey, sTime, eTime) {
      return getSingleVarMonitorData(moduleName, varKey, sTime, eTime);
    },
    getOption(data = []) {
      // 整理获得需要的数据
      const targetArr = data.map(oneDay => {
        const arr = oneDay.resultData; // 决策结果集合
        let passObj = arr.find(item => item.key === '通过') || {};
        let refauseObj = arr.find(item => item.key === '建议拒绝') || {};
        let isT5Obj = arr.find(item => item.key === 'T5逾期') || {};
        let notT5Obj = arr.find(item => item.key === '非T5逾期') || {};
        let isT10Obj = arr.find(item => item.key === 'T10逾期') || {};
        let notT10Obj = arr.find(item => item.key === '非T10逾期') || {};
        let t10Late;
        if (typeof isT10Obj.count === 'number' && typeof oneDay.count === 'number') {
          t10Late = (isT10Obj.count / oneDay.count) * 100;
        }
        let obj = {
          resultrate: oneDay.resultrate * 100, // 样本（通过率）
          count: oneDay.count, // 样本（总计）
          avg: oneDay.avg, // 平均值（总计）
          t10Late: t10Late, // 样本（t10指标逾期率）
          passingCount: oneDay.passingCount === null ? undefined : oneDay.passingCount, // '样本（通过总数）',
          time: oneDay.date.slice(0, 10), // 时间
          data: arr, // 决策结果集合
          pass: passObj.avg, // 平均值（通过）
          refause: refauseObj.avg, // 平均值（建议拒绝）
          isT5: isT5Obj.avg, // 平均值（T5指标逾期）
          notT5: notT5Obj.avg, // 平均值（非T5指标逾期）
          isT10: isT10Obj.avg, // 平均值（T5指标逾期）
          notT10: notT10Obj.avg // 平均值（非T5指标逾期）
        };
        Object.keys(obj).forEach(key => {
          if (typeof obj[key] === 'number') {
            if (!Number.isInteger(obj[key])) {
              obj[key] = obj[key].toFixed(2);
            }
          }
        });
        return obj;
      });
      const xData = targetArr.map(item => item.time);
      // 获取通过率和总数
      let seriesData = [];
      Object.keys(keyToName).forEach((name, i) => {
        let arr = [];
        for (let item of targetArr) {
          arr.push(item[name]);
        }
        let isLate = ['resultrate', 't10Late'].includes(name);
        let yAxisIndex = i > 3 ? 2 : isLate ? 1 : 0;
        seriesData.push({
          name: keyToName[name],
          type: 'line',
          lineStyle: {
            normal: {
              type: `${isLate ? 'dashed' : 'solid'}`
            }
          },
          yAxisIndex: yAxisIndex,
          smooth: true,
          data: arr
        });
      });
      const option = {
        color: colorArr,
        tooltip: {
          trigger: 'axis',
          formatter: params => {
            const arr = params.map(item => {
              let str = `${item.marker} ${item.seriesName}：`;
              if (typeof item.data !== 'undefined') {
                str += `${item.data}${item.seriesIndex === 2 || item.seriesIndex === 3 ? '%' : ''}`;
              } else {
                str += '-';
              }
              return str;
            });
            arr.unshift(params[0].name);
            return arr.join('<br />');
          }
        },
        legend: [
          {
            data: Object.values(keyToName).slice(0, 4),
            top: 0
          },
          {
            data: Object.values(keyToName).slice(4),
            top: 25
          }
        ],
        grid: {
          top: '20%',
          left: 70,
          right: '100px',
          bottom: '20%'
        },
        xAxis: {
          type: 'category',
          data: xData,
          scale: true,
          boundaryGap: false,
          axisLine: {
            onZero: false
          },
          splitLine: {
            show: false
          },
          splitNumber: 20,
          min: 'dataMin',
          max: 'dataMax'
        },
        yAxis: [
          {
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            name: '总计',
            nameGap: 8,
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '比率（%）',
            nameGap: 8,
            min: 0,
            position: 'right',
            offset: 70,
            axisLine: {
              lineStyle: {
                color: colorArr[2]
              }
            },
            boundaryGap: [0.2, 0.2]
          },
          {
            scale: true,
            splitArea: {
              show: true
            },
            name: '平均值',
            nameGap: 8,
            min: 0,
            position: 'right',
            axisLine: {
              lineStyle: {
                color: colorArr[4]
              }
            },
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: seriesData
      };
      if (xData.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return option;
    }
  }
};
</script>

<style scoped>
#univariate-monitor-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
