<template>
  <div class="decision-monitor-chart-container">
    <div id="decision-monitor-chart" v-show="showChart"></div>
    <div v-show="showChart" class="data-source">
      <span>蜂鸟</span>
    </div>
    <span class="nodata el-table__empty-block" v-if="!showChart">暂无数据</span>
  </div>
</template>

<script>
import {getProductMainModuleMonitor, getProductMonitorBySample} from '@/api/monitor.js';
import {getColorObj} from '@/assets/scripts/getSomeSetting.js';
import {yAxisFormatter} from '@/assets/scripts/utils.js';

const keyToName = {
  count: '主决策总数',
  passingCount: '通过总数',
  resultrate: '主决策通过率',
  mob3_recordrate: 'Mob3三次零还款指标比率',
  t10: 'T10指标逾期率（计算方式）'
};
const allColorObj = getColorObj();
const colorArr = Object.values(keyToName).map(key => allColorObj[key]);
export default {
  name: 'decision-monitor-chart',
  props: {},
  data() {
    return {
      showChart: false
    };
  },
  created() {
    this.$on('drawDecisionChart', (rcProductName, moduleName, sTime, eTime) => {
      this.drawFunc(rcProductName, moduleName, sTime, eTime);
    });
  },
  methods: {
    drawFunc(rcProductName, moduleName, sTime, eTime) {
      this.showChart = true;
      this.getData(rcProductName, moduleName, sTime, eTime)
        .then(result => {
          if (Array.isArray(result.result)) {
            if (!this.chart) {
              const elem = this.$el.querySelector('#decision-monitor-chart');
              this.chart = echarts.init(elem);
            }
            const option = this.getOption(result.result);
            this.chart.setOption(option);
          } else {
            this.showChart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showChart = false;
        });
    },
    getData(rcProductName, moduleName, sTime, eTime) {
      if (!sTime) {
        return getProductMainModuleMonitor(rcProductName, moduleName);
      } else {
        return getProductMonitorBySample(rcProductName, moduleName, sTime, eTime)
          .then(result => {
            return {
              result: result.result.map(item => item.data),
              statusCode: result.statusCode
            };
          })
          .catch(err => Promise.reject(err));
      }
    },
    getOption(data = []) {
      const xData = data.map(item => item.time);
      const lateArr = Object.keys(keyToName).map(key => {
        let arr = [];
        for (let item of data) {
          let value = item[key];
          if (typeof value === 'number') {
            value = Number.isInteger(value) ? value : value.toFixed(2);
          }
          arr.push(value);
        }
        return arr;
      });
      const seriesData = lateArr.map((item, i) => {
        const name = Object.values(keyToName)[i];
        return {
          name: name,
          yAxisIndex: i > 2 ? 3 : i,
          data: item,
          type: i > 1 ? 'line' : 'bar',
          barGap: 0,
          smooth: true
        };
      });
      const options = {
        color: colorArr,
        legend: [
          {
            data: Object.values(keyToName).slice(0, 3),
            top: 13
          },
          {
            data: Object.values(keyToName).slice(3),
            top: 38
          }
        ],
        tooltip: {
          trigger: 'axis',
          formatter: params => {
            const arr = params.map(item => {
              return `${item.marker} ${item.seriesName}：${item.data}${item.seriesIndex < 2 ? '人' : '%'}`;
            });
            if (params[0].name.indexOf('-') > -1) {
              arr.unshift(params[0].name);
            } else {
              arr.unshift(`${params[0].name.slice(0, 4)}-${params[0].name.slice(4)}`);
            }
            return arr.join('<br />');
          }
        },
        grid: {
          top: '20%',
          left: '15%',
          right: '15%',
          bottom: '20%'
        },
        xAxis: {
          type: 'category',
          data: xData,
          scale: true,
          boundaryGap: true,
          axisLine: {
            onZero: false
          },
          splitLine: {
            show: false
          },
          splitNumber: 20
        },
        yAxis: [
          {
            name: '决策人数',
            nameGap: 10,
            axisLine: {
              lineStyle: {
                color: colorArr[0]
              }
            },
            axisLabel: {
              formatter: yAxisFormatter
            },
            position: 'left',
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '通过总数',
            nameGap: 10,
            axisLine: {
              lineStyle: {
                color: colorArr[1]
              }
            },
            axisLabel: {
              formatter: yAxisFormatter
            },
            position: 'left',
            offset: 70,
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '通过率（%）',
            nameGap: 10,
            axisLine: {
              lineStyle: {
                color: colorArr[2]
              }
            },
            min: 0,
            max: 100,
            position: 'right',
            offset: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '逾期率（%）',
            nameGap: 10,
            min: 0,
            position: 'right',
            offset: 70,
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: seriesData
      };
      if (xData.length > 30) {
        options.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      return options;
    }
  }
};
</script>

<style scoped>
.decision-monitor-chart-container {
  height: 360px;
}
#decision-monitor-chart {
  position: relative;
  left: 0;
  right: 0;
  top: 0;
  bottom: 0;
  height: 360px;
}
</style>
