<template>
  <el-dialog title="保存" top="20vh" :visible.sync="dialogVisible" width="30%">
    <el-form label-width="60px">
      <el-form-item label="文件名">
        <el-input v-model="filename"></el-input>
      </el-form-item>
    </el-form>
    <span slot="footer" class="dialog-footer">
      <el-button type="primary" @click="saveFile">确 定</el-button>
    </span>
  </el-dialog>
</template>

<script>
export default {
  name: 'filename',
  props: {
    initVisible: {
      type: Boolean,
      required: true,
      default: false
    }
  },
  data() {
    return {
      filename: ''
    };
  },
  computed: {
    dialogVisible: {
      get: function() {
        return this.initVisible;
      },
      set: function(newV) {
        if (!newV) {
          this.filename = '';
          this.$emit('update:initVisible', newV);
        }
      }
    }
  },
  methods: {
    saveFile() {
      this.$emit('save', this.filename);
      this.dialogVisible = false;
      this.filename = '';
    }
  },
  created() {},
  mounted() {}
};
</script>

