<template>
  <div class="decision-monitor">
    <el-card v-show="showPart === 'form'">
      <div slot="header">
        <span class="card-name">决策过程监控</span>
        <el-popover placement="right" width="500" trigger="hover">
          <div>
            <p>通过样本和时间段的选择，监控该样本数据在某个决策时间段内的申请总数据和逾期指标情况。</p>
            <p>例如样本2018_卡卡贷全国版201711，决策监控到2018年11月份决策授信总数为142224人，超过还款日5天未还款用户占总放款人数比例为3.61%，通过率占51.95%。</p>
            <p>还可通过高级筛选变量、规则、结果作为筛选条件，点击查看监控，比如查看变量[FT]综合评分大于等于500分的监控记录如下图,返回点击重新分析按钮。</p>
          </div>
          <i class="el-icon-question" slot="reference"></i>
        </el-popover>
      </div>
      <el-form label-width="100px">
        <div class="flex-wrap">
          <el-form-item label-width="30px">
            <sample-select v-on:update:selected="getVars" :init-sample.sync="initSample"></sample-select>
          </el-form-item>
          <el-form-item label-width="30px">
            <datepicker
              :init-dates.sync="decisionDates"
              v-on:update:date="(newV) => decisionDates = newV"
            ></datepicker>
          </el-form-item>
          <el-form-item label-width="30px">
            <el-button type="primary" @click.prevent.stop="searchFunc">查看监控</el-button>
          </el-form-item>
        </div>
        <el-form-item label="高级筛选">
          <el-switch v-model="showFiltrate" active-color="#409eff"></el-switch>
        </el-form-item>
        <div v-show="showFiltrate">
          <el-form-item :label="`预筛选${index+1}`" v-for="(one,index) in prefilter" :key="index">
            <var-select
              v-on:update:selected="newV => {one.fieldName = newV[1]; one.initVar = newV}"
              :init-var.sync="one.initVar"
              :var-list.sync="varList"
              style="margin-right: 30px;"
            ></var-select>
            <el-select class="select-compare" v-model.trim="one.comparison" placeholder="请选择">
              <el-option
                v-for="(item, index) in operatorlist"
                :key="index"
                :label="item.text"
                :value="item.value"
              ></el-option>
            </el-select>
            <el-input v-model.trim="one.value" style="width: 220px; margin-right: 30px;"></el-input>
            <span class="btn-comm-add" @click.stop="addPrefilter" title="添加预筛选" v-if="index===0"></span>
            <span class="btn-comm-del" @click.stop="delPrefilter(index)" title="删除" v-if="index>0"></span>
          </el-form-item>
        </div>
      </el-form>
      <sample-chart ref="sampleChart" class="box-sample-chart"></sample-chart>
    </el-card>

    <div v-show="showPart === 'result'">
      <div class="return-page">
        <a href @click.prevent.stop="returnPrev">重新分析</a>
      </div>
      <div class="parm-des">
        <span>{{parmDes}}</span>
      </div>
      <el-card>
        <div slot="header">
          <span class="card-name">决策监控图</span>
        </div>
        <decision-monitor-chart ref="decisionChart"></decision-monitor-chart>
      </el-card>

      <el-card v-if="varList.length">
        <div slot="header">
          <span class="card-name">{{ moduleName }} T10逾期指标监控</span>
          <el-popover placement="right" width="400" trigger="hover">
            <div>
              <p>“样本名称”+T10逾期各个指标的监控情况分析</p>
              <p>选中T10逾期的变量指标，查看各个变量值对应的逾期情况和占比</p>
              <p>选中T10逾期的规则指标，查看各个规则对应的逾期情况和占比</p>
              <p>选中T10逾期的结果指标，查看各个结果对应的逾期情况和占比</p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </div>
        <div class="flex-wrap">
          <div class="flex-wrap col-flex">
            <el-tabs v-model="transmittype">
              <el-tab-pane label="变量" name="1">
                <div class="flex-wrap col-flex var-items">
                  <span
                    class="var"
                    :class="{active: item.isActive}"
                    v-for="(item, index) in varFilterList"
                    :key="index"
                    @click.prevent.stop="changeVar(item, $event)"
                  >
                    <span>{{item.label}}</span>
                  </span>
                </div>
              </el-tab-pane>
              <el-tab-pane label="规则" name="0">
                <div class="flex-wrap col-flex var-items">
                  <span
                    class="var"
                    :class="{active: item.isActive}"
                    v-for="(item, index) in varFilterList"
                    :key="index"
                    @click.prevent.stop="changeVar(item, $event)"
                  >
                    <span>{{item.label}}</span>
                  </span>
                </div>
              </el-tab-pane>
              <el-tab-pane label="结果" name="2">
                <div class="flex-wrap col-flex var-items">
                  <span
                    class="var"
                    :class="{active: item.isActive}"
                    v-for="(item, index) in varFilterList"
                    :key="index"
                    @click.prevent.stop="changeVar(item, $event)"
                  >
                    <span>{{item.label}}</span>
                  </span>
                </div>
              </el-tab-pane>
            </el-tabs>
          </div>
          <div class="t10-chart-container">
            <univariate-monitor-chart ref="univariateMonitorChart" class="univariate-monitor-box"></univariate-monitor-chart>
            <div id="t10-chart" v-show="showT10Chart"></div>
            <div v-show="showT10Chart" class="data-source">
              <span>蜂鸟</span>
            </div>
          </div>
          <el-popover
            v-show="totalIV"
            class="el-popper__custom"
            placement="right"
            width="400"
            trigger="hover"
          >
            <div>
              <div class="el-popover__title">
                <img src="../assets/icon-brow.png" alt>蜂鸟智能
              </div>
              <p>
                <b style="color: #409eff">{{vbname}} 自动IV值：</b>
                <span>{{totalIV}}</span>
              </p>
            </div>
            <img src="../assets/bird-2.gif" alt slot="reference">
          </el-popover>
        </div>
      </el-card>

      <el-card>
        <div slot="header">
          <span class="card-name">决策过程监控</span>
          <el-popover placement="right" width="400" trigger="hover">
            <div>
              <p>决策拒接原因监控可查看到所选样本中所命中的规则</p>
            </div>
            <i class="el-icon-question" slot="reference"></i>
          </el-popover>
        </div>
        <el-table :data="rcrefuseList" stripe style="width: 100%" :header-row-style="headerStyle">
          <el-table-column prop="variableName" label="命中规则名称" align="center"></el-table-column>
          <el-table-column prop="count" label="总数" width="120" align="center"></el-table-column>
          <el-table-column prop="refuserate" label="命中规则比率" align="center"></el-table-column>
        </el-table>
      </el-card>

      <div
        v-if="totalIV && showTooltip"
        role="tooltip"
        id="el-popover-8988"
        aria-hidden="false"
        class="el-popover el-popper"
        tabindex="0"
        style="width: 256px; position: absolute;  transform-origin: left center 0px; z-index: 2015;"
        :style="tooltipStyle"
        x-placement="right"
      >
        <div>
          <div class="el-popover__title">
            <img src="../assets/icon-brow.png" alt>蜂鸟智能
          </div>
          <b style="color: rgb(64, 158, 255);">自动IV值：</b>
          <span>{{totalIV}}</span>
        </div>
        <div x-arrow class="popper__arrow" style="top: 32px;"></div>
      </div>

      <div v-show="showsave" class="box-save-export">
        <el-button type="primary" @click.stop.prevent="saveResult">保存</el-button>
      </div>

      <filename @save="saveFile" :init-visible.sync="showfilesave"></filename>
    </div>
  </div>
</template>

<script>
import dayjs from 'dayjs';
import datepicker from '@/components/datepicker';
import sampleSelect from '@/components/sample-select.vue';
import varSelect from '@/components/var-select.vue';
import decisionMonitorChart from '@/components/decision-monitor-chart';
import univariateMonitorChart from '@/components/univariate-monitor-chart';
import sampleChart from '@/components/sample-chart';
import filename from '@/components/filename';
import {getColorObj, getOnePrefilter, getOperatorList} from '@/assets/scripts/getSomeSetting.js';
import {getVarList} from '@/api/sample.js';
import {queryDecision} from '@/api/decision.js';
import {getProductMonitorBySample, getRcrefuseList} from '@/api/monitor.js';
import {saveAnalysis, queryAnalysisDetail, queryVarAutoSplit} from '@/api/analysis.js';

const allColorObj = getColorObj();
const colorArr2 = ['占比', '一个月前占比', '逾期率', '一个月前逾期率'].map(key => allColorObj[key]);

export default {
  name: 'decision-monitor',
  components: {
    datepicker,
    sampleSelect,
    varSelect,
    decisionMonitorChart,
    univariateMonitorChart,
    sampleChart,
    filename
  },
  data() {
    return {
      showFiltrate: false,
      //预筛选默认的一条
      prefilter: [getOnePrefilter()],
      //预筛选的运算条件可选的内容
      operatorlist: getOperatorList(),
      headerStyle: {
        color: '#409eff'
      },
      showTooltip: false,
      tooltipStyle: {
        top: 0,
        left: 0
      },
      searched: false,
      showT10Chart: false,
      showSampleChart: false,
      showDecisionChart: false,
      showsave: false,
      showfilesave: false,
      initSample: [],
      decisionDates: [
        dayjs()
          .add(-3, 'month')
          .format('YYYY-MM-DD'),
        dayjs().format('YYYY-MM-DD')
      ],
      moduleName: '',
      moduleList: [],
      transmittype: '1',
      varList: [],
      varFilterList: [],
      monitorList: [],
      varsetList: [],
      moduleDataList: [],
      rcrefuseList: [],
      id: '',
      vbname: '',
      totalIV: ''
    };
  },
  computed: {
    showPart() {
      return this.$store.state.monitorPage.decision;
    },
    parmDes() {
      return this.moduleName;
    }
  },
  watch: {
    transmittype: function(newV) {
      this.varFilterList = this.varList.filter(item => item.transmittype === String(newV));
    },
    showPart: function(newV) {
      if (newV === 'form') {
        let initSample = this.initSample;
        this.$nextTick(() => {
          if (!this.$refs.sampleChart.showChart) {
            this.initSample = [];
            this.$refs.sampleChart.$emit('drawSampleChart', this.moduleName);
            this.$nextTick(() => {
              this.initSample = initSample;
              console.log(this.initSample);
            });
          }
        });
      }
    },
    $route: {
      handler: function(to, from) {
        if (
          from &&
          ((from.name === 'decision-monitor-view' && to.name === 'decision-monitor') ||
            (from.name === 'decision-monitor' && to.name === 'decision-monitor-view'))
        ) {
          this.$store.commit('UPDATE_MONITORPAGE', {
            key: 'decision',
            value: 'form'
          });
          this.$router.replace({
            path: '/empty',
            query: {
              callbackurl: to.path
            }
          });
        }
      },
      immediate: true
    }
  },
  created() {
    this.recordId = this.$route.params.recordid;
    if (this.recordId) {
      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'decision',
        value: 'result'
      });
      this.getAnalysisResult();
    }
  },
  mounted() {
    let modulename = this.$route.query.modulename;
    let productname = this.$route.query.productname;
    let moduletype = String(this.$route.query.moduletype);
    if (modulename != null && productname != null && moduletype != null) {
      this.params = {};
      this.$nextTick(() => {
        this.getVars([productname, moduletype, modulename], true);
      });
    }
  },
  methods: {
    returnPrev() {
      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'decision',
        value: 'form'
      });
    },
    getVars(val, isInit = false) {
      this.rcProductName = val[0];
      this.moduleType = val[1];
      this.moduleName = val[2];
      this.initSample = val;
      if (!isInit) {
        this.$refs.sampleChart.$emit('drawSampleChart', this.moduleName);
      }
      getVarList(this.moduleName)
        .then(result => {
          if (Array.isArray(result.result)) {
            this.varList = result.result.map(item => {
              item.label = `${item.vbname}${item.fieldtype === 'float' ? '（连续）' : '（离散）'}`;
              item.value = item.key;
              return item;
            });
            for (let item of this.varList) {
              this.$set(item, 'isActive', false);
            }

            if (isInit) {
              if (this.params.filterConditionList && this.params.filterConditionList.length > 0)
                this.prefilter = this.params.filterConditionList;
              if (this.params.theVar) {
                const theVar = this.params.theVar;
                this.transmittype = theVar.transmittype;
                this.varFilterList = this.varList.filter(one => (one.transmittype = theVar.transmittype));
                this.varFilterList.forEach(item => {
                  if (item.key === theVar.key) {
                    console.log(theVar);
                    this.$nextTick(() => {
                      Object.assign(item, theVar);
                      this.getT10Data(item);
                    });
                  }
                });
              } else {
                this.varFilterList = this.varList.filter(one => one.transmittype === this.transmittype);
                this.searchFunc();
              }
            } else {
              this.varFilterList = this.varList.filter(one => one.transmittype === this.transmittype);
            }
            return;
          }
          this.varList = [];
        })
        .catch(err => {
          console.log(err);
          this.varList = [];
        });
    },
    addPrefilter() {
      this.prefilter.push(getOnePrefilter());
    },
    delPrefilter(index) {
      this.prefilter.splice(index, 1);
    },
    searchFunc() {
      if (!this.moduleName) {
        this.$message.warning('请选择样本名称');
        return;
      }
      if (!this.decisionDates[0]) {
        this.$message.warning('请选择起始时间');
        return;
      }
      if (!this.decisionDates[1]) {
        this.$message.warning('请选择结束时间');
        return;
      }
      let prefilter = this.prefilter.filter(function(v) {
        return v.comparison.length > 0 && v.fieldName.length > 0 && v.value.length > 0;
      });
      let filterConditionList = this.showFiltrate ? prefilter : [];

      this.$store.commit('UPDATE_MONITORPAGE', {
        key: 'decision',
        value: 'result'
      });

      this.$nextTick(() => {
        this.showsave = true;
        try {
          this.$refs.decisionChart.$emit(
            'drawDecisionChart',
            this.rcProductName,
            this.moduleName,
            this.decisionDates[0],
            this.decisionDates[1]
          );
        } catch (err) {
          this.showsave = false;
        }

        this.changeVar(this.varFilterList[0]);

        getRcrefuseList(this.moduleName, this.decisionDates[0], this.decisionDates[1], filterConditionList)
          .then(result => {
            if (Array.isArray(result.result) && result.result.length) {
              this.rcrefuseList = result.result.map(item => {
                let num = item.refuserate;
                if (typeof num === 'number') {
                  num = Number.isInteger(num) ? num : num.toFixed(2);
                }
                item.refuserate = `${num}%`;
                return item;
              });
              return;
            }
            this.showsave = false;
            this.rcrefuseList = [];
          })
          .catch(err => {
            console.log(err);
            this.showsave = false;
            this.rcrefuseList = [];
          });
      });
    },
    changeVar(item = {}, event) {
      this.vbname = item.vbname;
      if (item.isActive) {
        return;
      }
      this.$refs.univariateMonitorChart.$emit(
        'drawUnivariateMonitorChart',
        this.moduleName,
        item.key,
        this.decisionDates[0],
        this.decisionDates[1]
      );
      if (item.fieldtype === 'float') {
        queryVarAutoSplit(this.moduleName, item.key)
          .then(result => {
            this.totalIV = result.result && result.result.totalIV.toFixed(2);
            if (Array.isArray(result.result.variableAreaRangeList)) {
              item.rangeList = result.result.variableAreaRangeList;
              item.statisticType = 'SMART_RANGE';
            }
            this.getT10Data(item);
          })
          .catch(err => {
            this.getT10Data(item);
            this.totalIV = '';
            console.log(err);
          });
      } else {
        this.getT10Data(item);
        this.totalIV = '';
      }

      const scrollTop = window.pageYOffset || document.documentElement.scrollTop || document.body.scrollTop;
      if (event) {
        const position = event.target.getBoundingClientRect();
        this.showTooltip = true;
        this.tooltipStyle = {
          left: position.left + position.width + 'px',
          top: position.top + scrollTop - 29 + 'px'
        };
      }
    },

    getT10Data(item) {
      if (!this.decisionDates[0]) {
        this.$message.warning('请选择起始时间');
        return;
      }
      if (!this.decisionDates[1]) {
        this.$message.warning('请选择结束时间');
        return;
      }
      const theOne = this.varList.find(one => one.isActive);
      if (theOne) {
        theOne.isActive = false;
      }
      item.isActive = true;
      const data = {
        indexName: [this.moduleName],
        rcProductName: this.rcProductName,
        moduleType: String(this.moduleType),
        variablekey: item.key,
        createTimeStart: this.decisionDates[0],
        createTimeEnd: this.decisionDates[1],
        statisticConditionList: [
          {
            fieldName: item.key,
            statisticType: item.statisticType || 'FIELD_GROUP',
            rangeList: item.rangeList || []
          }
        ]
      };
      // 占比计数百分比
      let p1 = this.getStatistic('BEFORE_LOAN', data);
      // 一个月前占比
      let p2 = this.getStatistic(
        'BEFORE_LOAN',
        Object.assign({}, data, {
          createTimeStart: dayjs(this.decisionDates[0])
            .add(-1, 'month')
            .format('YYYY-MM-DD'),
          createTimeEnd: dayjs(this.decisionDates[0])
            .add(-1, 'day')
            .format('YYYY-MM-DD')
        })
      );
      // T10逾期率
      let p3 = this.getStatistic('AFTER_LOAN', data);
      // 一个月前T10逾期率
      let p4 = this.getStatistic(
        'AFTER_LOAN',
        Object.assign({}, data, {
          createTimeStart: dayjs(this.decisionDates[0])
            .add(-1, 'month')
            .format('YYYY-MM-DD'),
          createTimeEnd: dayjs(this.decisionDates[0])
            .add(-1, 'day')
            .format('YYYY-MM-DD')
        })
      );
      Promise.all([p1, p2, p3, p4])
        .then(results => {
          this.showT10Chart = false;
          setTimeout(() => {
            this.showTooltip = false;
          }, 3000);
          let isError = false;
          let beforeData = [];
          let afterData = [];
          let beforeData1 = [];
          let afterData1 = [];
          if (
            results[0].result &&
            Array.isArray(results[0].result.originalReport) &&
            results[0].result.originalReport.length
          ) {
            beforeData = results[0].result.originalReport;
          }
          if (
            results[1].result &&
            Array.isArray(results[1].result.originalReport) &&
            results[1].result.originalReport.length
          ) {
            beforeData1 = results[1].result.originalReport;
          }
          if (
            results[2].result &&
            Array.isArray(results[2].result.originalReport) &&
            results[2].result.originalReport.length
          ) {
            afterData = results[2].result.originalReport;
          }
          if (
            results[3].result &&
            Array.isArray(results[3].result.originalReport) &&
            results[3].result.originalReport.length
          ) {
            afterData1 = results[3].result.originalReport;
          }
          const arr = beforeData.map(val => {
            let theOne = beforeData1.find(one => one.key === val.key);
            let theOne1 = afterData.find(one => one.key === val.key);
            let theOne2 = afterData1.find(one => one.key === val.key);
            let t10V, t10V1, t10V2;
            if (theOne) {
              t10V = parseFloat(theOne.value);
              if (typeof t10V === 'number') {
                t10V = Number.isInteger(t10V) ? t10V : t10V.toFixed(2);
              }
            }
            if (theOne1) {
              t10V1 = parseFloat(theOne1.value);
              if (typeof t10V1 === 'number') {
                t10V1 = Number.isInteger(t10V1) ? t10V1 : t10V1.toFixed(2);
              }
            }
            if (theOne2) {
              t10V2 = parseFloat(theOne2.value);
              if (typeof t10V2 === 'number') {
                t10V2 = Number.isInteger(t10V2) ? t10V2 : t10V2.toFixed(2);
              }
            }
            return {
              key: val.key,
              v1: parseFloat(val.value),
              v2: t10V,
              v3: t10V1,
              v4: t10V2
            };
          });
          console.log(arr);
          if (arr.length) {
            this.showT10Chart = true;
            this.$nextTick(() => {
              this.drawT10Chart(arr, item.vbname);
            });
          } else {
            this.showT10Chart = false;
          }
        })
        .catch(err => {
          console.log(err);
          this.showT10Chart = false;
          setTimeout(() => {
            this.showTooltip = false;
          }, 3000);
        });
    },
    getStatistic(type, data) {
      let reqData = Object.assign({}, data, {
        resultValueType: 'COUNT',
        filterConditionList: [],
        resultField: '',
        resultNeedPercent: true
      });
      if (type === 'BEFORE_LOAN') {
        Object.assign(reqData, {
          loanType: 'BEFORE_LOAN'
        });
      } else if (type === 'AFTER_LOAN') {
        Object.assign(reqData, {
          afterLoanKPI: 'T10',
          overdueDays: 'null',
          loanType: 'AFTER_LOAN'
        });
      }
      return queryDecision(reqData);
    },
    drawT10Chart(data = [], varname) {
      if (!this.t10chart) {
        this.t10chart = echarts.init(document.getElementById('t10-chart'));
      }

      let option = {
        title: {
          top: 0,
          text: varname + 'T10逾期指标监控图',
          left: 'center',
          textStyle: {
            fontWeight: 'bold',
            fontSize: 16
          }
        },
        color: colorArr2,
        legend: {
          data: ['占比', '一个月前占比', '逾期率', '一个月前逾期率'],
          top: 40,
          left: 60
        },
        tooltip: {
          trigger: 'axis',
          axisPointer: {
            type: 'shadow'
          },
          formatter: params => {
            const arr = params.map(item => {
              return `${item.marker} ${item.seriesName}：${item.data}%`;
            });
            arr.unshift(params[0].name);
            return arr.join('<br />');
          },
          padding: [5, 12]
        },
        grid: {
          top: '30%',
          left: 60,
          right: 70,
          bottom: '20%',
          containLabel: true
        },
        xAxis: [
          {
            type: 'category',
            name: '变量取值',
            nameGap: 30,
            nameLocation: 'center',
            scale: true,
            boundaryGap: true,
            axisLine: {
              onZero: false
            },
            splitLine: {
              show: false
            },
            // splitNumber: 20,
            data: data.map(v => v.key)
          }
        ],
        yAxis: [
          {
            name: '占比（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr2[0]
              }
            },
            min: 0,
            boundaryGap: [0.2, 0.2]
          },
          {
            name: '逾期率（%）',
            scale: true,
            splitArea: {
              show: true
            },
            axisLine: {
              lineStyle: {
                color: colorArr2[1]
              }
            },
            min: 0,
            position: 'right',
            boundaryGap: [0.2, 0.2]
          }
        ],
        series: [
          {
            name: '占比',
            type: 'bar',
            yAxisIndex: 0,
            barGap: 0,
            data: data.map(v => v.v1)
          },
          {
            name: '一个月前占比',
            type: 'bar',
            yAxisIndex: 0,
            data: data.map(v => v.v2)
          },
          {
            name: '逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v3)
          },
          {
            name: '一个月前逾期率',
            type: 'line',
            yAxisIndex: 1,
            data: data.map(v => v.v4)
          }
        ]
      };
      if (option.series[0].data.every(v => isNaN(v)) && option.series[1].data.every(v => isNaN(v))) {
        console.log('======有非数字值存在=====');
        this.showT10Chart = false;
        return;
      }

      if (option.xAxis[0].data.length > 30) {
        option.dataZoom = [
          {
            type: 'slider',
            y: '90%',
            start: 0,
            end: 100
          }
        ];
      }
      this.t10chart.setOption(option);
    },
    saveResult() {
      if (!this.filesaveid) {
        this.showfilesave = true;
        return;
      }
      this.saveFile(this.filename);
    },
    saveFile(name) {
      let data = {};
      data.parmDes = this.moduleName;
      data.name = name;
      data.type = 3;
      data.id = this.filesaveid;
      let prefilter = this.prefilter.filter(function(v) {
        return v.comparison.length > 0 && v.fieldName.length > 0 && v.value.length > 0;
      });
      const theVar = this.varFilterList.find(item => item.isActive === true);
      console.log(theVar);
      const params = {
        initSample: this.initSample,
        modulename: this.moduleName,
        filterConditionList: this.showFiltrate ? prefilter : [],
        theVar: theVar,
        totalIV: this.totalIV,
        tooltipStyle: this.tooltipStyle,
        startTime: this.decisionDates[0],
        endTime: this.decisionDates[1]
      };
      data.result = JSON.stringify(params);
      saveAnalysis(data)
        .then(result => {
          this.$message.success('保存成功');
          this.filename = data.name;
          this.filesaveid = result.result;
        })
        .catch(err => console.log(err));
    },
    getAnalysisResult() {
      queryAnalysisDetail(this.recordId)
        .then(result => {
          if (result.result == null) {
            this.$message.warning('此历史记录不存在');
            return;
          }
          this.dealWithParams(result.result);
        })
        .catch(err => console.log(err));
    },
    dealWithParams(result) {
      this.filename = result.name;
      this.filesaveid = result.id;
      const params = JSON.parse(result.result);
      this.initSample = params.initSample;
      this.decisionDates = [params.startTime, params.endTime];
      this.moduleName = params.modulename;
      this.totalIV = params.totalIV;
      if (this.totalIV) {
        this.showTooltip = true;
      }
      this.tooltipStyle = params.tooltipStyle;
      this.params = params;

      this.getVars(this.initSample, true);

      this.searchFunc();
    }
  }
};
</script>

